import React, {Component} from "react";
import {connect} from "react-redux";
import FixTVModal from "../FixTvModal";
import {FixAnalyzerAdapter} from "../AnalyzerAdapter";
import {FloatingBox, LabelCell, ButtonCell} from "../../BoxFragments";
import {GraphicCell} from "../../Utils";
import {changeTvModalVisibility} from "../../../../../actions/cart";
import {getCustomFilters, getTvModalVisibility} from "../../../../../selectors";
import {createCustomRules} from "../../../../../analyzer/TvPackageUtils";

const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
};

class AddTVButton extends Component {

    constructor(props){
        super(props);
        this.addButtonClicked = this.addButtonClicked.bind(this);
        this.closeModalClicked = this.closeModalClicked.bind(this);
    }

    addButtonClicked() {
        this.props.changeTvModalVisibility(true);
    }

    removeButtonClicked(productCode) {
        if(this.adapter && this.adapter.removeProduct){
            this.adapter.removeProduct(productCode);
        }
    }

    closeModalClicked() {
        this.props.changeTvModalVisibility(false);
    }

    getIncludedInThePackageTitle() {
        if(this.props.descriptions && this.props.descriptions.includedInThePackageTitle && this.props.descriptions.includedInThePackageTitle !== ""){
            return this.props.descriptions.includedInThePackageTitle;
        }
        return "W cenie pakietu";
    }

    render(){
        let subModalTitle = this.props.descriptions ? this.props.descriptions.modalTitle : "";
        let subModalContents = this.props.descriptions ? this.props.descriptions.modalContents : "";
        let migratedTvPacks = this.props.migratedTvPacks ? this.props.migratedTvPacks : [];
        return(
            <div>
                <FloatingBox boxClassName="u-padding-left-l u-padding-right-l u-box-shadow--s u-box-shadow-hover">
                    <a href="#" onClick={preventDefaultWrapper(this.addButtonClicked)}>
                        <table className="u-table-fixed">
                            <tbody>
                                <tr>
                                    <GraphicCell icon={this.props.icon}/>
                                    <LabelCell label={this.props.label}/>
                                    <ButtonCell icon={"plus"} link={"#"}/>
                                </tr>
                            </tbody>
                        </table>
                    </a>
                </FloatingBox>
                <FixAnalyzerAdapter
                    configurables={this.props.configurables}
                    entry={this.props.entry}
                    customRules={createCustomRules(this.props.presentable, migratedTvPacks, this.props.customFilters)}
                    ref={(adapter) => this.adapter = adapter}
                    triggerCartRecalculation={this.props.tvModalVisibility}
                >
                    <FixTVModal
                        label={this.props.header}
                        open={this.props.tvModalVisibility}
                        services={this.props.presentable}
                        migratedServices={this.props.configurables.migratedProducts}
                        onClose={this.closeModalClicked}
                        productList={this.props.inner}
                        subModalTitle={subModalTitle}
                        subModalContents={subModalContents}
                        showNetPrices={this.props.showNetPrices}
                        includedInThePackageTitle={this.getIncludedInThePackageTitle()}
                        filtersVisible={true}
                    />
                </FixAnalyzerAdapter>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    changeTvModalVisibility: (visibility) => dispatch(changeTvModalVisibility(visibility))
});

const mapStateToProps = state => ({
    tvModalVisibility: getTvModalVisibility(state),
    customFilters: getCustomFilters(state)
});

export default connect(mapStateToProps, mapDispatchToProps, null, {withRef: true})(AddTVButton);